body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

button.winner {
  background-color: #585656;
}

.chosen-btn {
  font-weight: bold;
}

button.square[data-pro="X"]
{
  color: red;
}

button.square[data-pro="O"]{
  color: green;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {

  height: 30px;
  line-height: 30px;
  text-align: center;
  font-size: 17px;
  font-weight: 520;
  
}

.square {
  background: #fff;
  border: 1px solid rgb(66, 18, 128);
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
  text-align: left;
 
}
